


































import {Component, Mixins} from "vue-property-decorator";
import att_mixins from "@/mixins/basic";
import render from "@/interface/render";
import search from "@/components/search/search.vue";
import module_init_args from "@/store/modules/init_args";

@Component({components: {search}})
export default class my_order extends Mixins(att_mixins) {

    columns = [
        {
            title: '工单编号:',
            key: 'work_id',
            sortable: true
        },
        {
            title: '工单说明',
            key: 'text',
            tooltip: true
        },
        {
            title: '工单类型',
            key: 'type',
            render: render.type
        },
        {
            title: '是否备份',
            key: 'backup',
            render: render.backup
        },
        {
            title: '提交时间:',
            key: 'date',
            sortable: true
        },
        {
            title: '当前操作人',
            key: 'assigned',
            sortable: true
        },
        {
            title: '状态',
            key: 'status',
            render: render.tag,
            sortable: true
        },
        {
            title: '操作',
            key: 'action',
            align: 'center',
            slot: 'action',
            width: 300
        }
    ];

    url = `${this.$config.url}/user/order`

    current_page(vl = 1) {
        this.fetch_page(vl, this.url)
    }

    openOrder(row: any) {
        module_init_args.fetch_order_item(row)
        this.$router.push({
            name: 'profile',
        })
    }

    mounted() {
        this.current_page()
    }
}
